import { FunctionComponent } from 'preact';
import { Card, Skeleton, SkeletonRow } from '@adobe-commerce/elsie/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import { classes } from '@adobe-commerce/elsie/lib';
import { CustomerCompanyInfoCardProps } from '@/company-management/types';
import './CustomerCompanyInfoCard.css';

/**
 * Component for displaying customer company information in a card format
 * Shows company name, job title, and work phone number with loading states
 */
export const CustomerCompanyInfoCard: FunctionComponent<CustomerCompanyInfoCardProps> = ({
  customerCompanyInfo,
  loading,
  className,
}) => {
  const translations = useText({
    companyName: 'Company.shared.fields.companyName',
    jobTitle: 'Company.shared.fields.jobTitle',
    workPhoneNumber: 'Company.shared.fields.workPhoneNumber',
    userRole: 'Company.shared.fields.userRole',
  });

  // Return loader card OR data card
  if (loading) {
    return (
      <Card variant="secondary" className={classes(['customer-company-info-card', className])}>
        <div className="customer-company-info-card__content">
          <Skeleton data-testid="customerCompanyInfoSkeletonLoader">
            <SkeletonRow variant="heading" size="xlarge" fullWidth={false} lines={1} />
            <SkeletonRow variant="heading" size="xlarge" fullWidth={true} lines={1} />
            <SkeletonRow variant="heading" size="xlarge" fullWidth={true} lines={1} />
            <SkeletonRow variant="heading" size="xlarge" fullWidth={true} lines={1} />
          </Skeleton>
        </div>
      </Card>
    );
  }

  if (!customerCompanyInfo) {
    return null;
  }

  return (
    <Card variant="secondary" className={classes(['customer-company-info-card', className])}>
      <div className="customer-company-info-card__content">
        <p>{translations.companyName}: {customerCompanyInfo.companyName}</p>
        
        {customerCompanyInfo.jobTitle && (
          <p>{translations.jobTitle}: {customerCompanyInfo.jobTitle}</p>
        )}
        
        {customerCompanyInfo.workPhoneNumber && (
          <p>{translations.workPhoneNumber}: {customerCompanyInfo.workPhoneNumber}</p>
        )}
        
        {customerCompanyInfo.userRole && (
          <p>{translations.userRole}: {customerCompanyInfo.userRole}</p>
        )}
      </div>
    </Card>
  );
};
