import { Container } from '@adobe-commerce/elsie/lib';
import { CustomerCompanyInfoCard } from '@/company-management/components';
import { useCustomerCompanyInfo } from '@/company-management/hooks/containers';
import { CustomerCompanyInfoProps } from '@/company-management/types';

/**
 * Container component for customer company information
 * Manages data fetching and passes props to CustomerCompanyInfoCard
 */
export const CustomerCompanyInfo: Container<CustomerCompanyInfoProps> = ({
  className,
}) => {
  const { companyInfo: customerCompanyInfo, loading } = useCustomerCompanyInfo();

  return <CustomerCompanyInfoCard customerCompanyInfo={customerCompanyInfo} loading={loading} className={className} />;
};
